:root {
  --black: #111;
  --orange: #fd9222;
  --blackish: #1e1823;
  --beige: #fafafa;
  --pureblack: #000;
  --grey: #111111b3;
  --light-orange: #fcf1e0;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

ul {
  margin: 0;
  padding-left: 0;
  list-style: none;
}

h1, h2, h3, h4, h5, h6, p, a {
  margin: 0;
}

body {
  letter-spacing: -.28px;
  font-family: Montserrat;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
}

h1 {
  letter-spacing: -.84px;
  font-family: Montserrat;
  font-size: 28px;
  font-style: normal;
  font-weight: 500;
  line-height: 1.21;
}

h2 {
  letter-spacing: -.72px;
  text-transform: uppercase;
  font-family: Montserrat;
  font-size: 24px;
  font-style: normal;
  font-weight: 600;
  line-height: 1.16;
}

h3 {
  letter-spacing: -.6px;
  text-transform: uppercase;
  font-family: Montserrat;
  font-size: 20px;
  font-style: normal;
  font-weight: 600;
  line-height: 24px;
}

h4 {
  font-family: Montserrat;
  font-size: 18px;
  font-style: normal;
  font-weight: 600;
  line-height: normal;
}

.list {
  list-style: none;
}

@media (min-width: 375px) {
