.menu-toggle {
  min-height: 40px;
  min-width: 40px;
  height: 50px;
  width: 50px;
  cursor: pointer;
  background-color: #0000;
  border: none;
  border-radius: 50%;
  outline: none;
  align-items: center;
  margin: 0;
  padding: 0;
  display: flex;
}

.menu-container {
  width: 236px;
  height: 100%;
  z-index: 999;
  background-color: #1e1823;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  padding-top: 150px;
  font-size: 16px;
  transition: transform .25s ease-in-out;
  display: flex;
  position: fixed;
  top: 0;
  right: 0;
  transform: translateX(100%);
}

.menu-container.is-open {
  opacity: 1;
  visibility: visible;
  transform: translateX(0);
}

.mobile-menu {
  flex-direction: column;
  gap: 15px;
  padding-right: 30px;
  list-style: none;
  display: inline-flex;
}

.mobile-menu .link:after {
  content: "";
  height: .3px;
  width: 0;
  background-color: #fd9222;
  margin: auto;
  display: flex;
  position: absolute;
}

.mobile-menu .link:hover:after {
  width: 50%;
  transition: width .3s linear;
}

.mobile-menu .link {
  color: #fff;
  letter-spacing: .72px;
  text-transform: capitalize;
  padding: 10px;
  font-size: 14px;
  font-style: normal;
  font-weight: 700;
  line-height: 24px;
  text-decoration: none;
}

.mobile-menu .link:hover {
  color: #fd9222;
}

.icons-toggle {
  justify-content: left;
  gap: 18px;
  padding-right: 150px;
  list-style: none;
  display: inline-flex;
}

.toggle-svg:hover {
  filter: brightness(0) saturate() invert(71%) sepia(43%) saturate(4333%) hue-rotate(344deg) brightness(104%) contrast(98%);
}

.js-close-button {
  width: 24px;
  height: 24px;
  cursor: pointer;
  background-color: #0000;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  transition: background-color .25s cubic-bezier(.4, 0, .2, 1), border .25s cubic-bezier(.4, 0, .2, 1);
  display: flex;
  position: absolute;
  top: 24px;
  right: 24px;
}

.js-close-button:hover {
  filter: brightness(0) saturate() invert(71%) sepia(43%) saturate(4333%) hue-rotate(344deg) brightness(104%) contrast(98%);
}

