  line-height: 18px;
}

.subscribe-button {
  color: #fff;
  cursor: pointer;
  min-width: 169px;
  background-color: #fd9222;
  border: none;
  border-radius: 100px;
  margin: 0 auto;
  padding: 16px 32px;
  font-family: Montserrat;
  font-size: 14px;
  font-style: normal;
  font-weight: 600;
  line-height: normal;
  transition: background-color .25s cubic-bezier(.4, 0, .2, 1);
  display: block;
  box-shadow: 0 4px 4px #00000026;
}

.subscribe-button:hover, .subscribe-button:focus {
  color: #fd9222;
  background-color: #fcf1e0;
}

.subscribe-modal {
  width: 311px;
  height: 350px;
  background: #fff;
  border-radius: 15px;
  transition: transform .25s cubic-bezier(.4, 0, .2, 1);
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.subscribe-close-btn {
  color: #fff;
  min-height: 24px;
  min-width: 24px;
  z-index: 1;
  cursor: pointer;
  background: none;
  justify-content: center;
  align-items: center;
  padding: 0;
  transition: background-color .25s cubic-bezier(.4, 0, .2, 1), border .25s cubic-bezier(.4, 0, .2, 1);
  display: flex;
  position: absolute;
  top: 15px;
  right: 14px;
}

.subscribe-close-btn:hover, .subscribe-close-btn:focus {
  fill: #fd9222;
  border: none;
}

.subscribe-close-icon {
  transition: fill .25s cubic-bezier(.4, 0, .2, 1);
}

.is-hidden {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
}

.is-hidden .subscribe-modal {
  transform: translateY(100%);
}

.subscribe-image {
  height: 169px;
